<template>
  <div class="Pechart">
    <div class="echart" ref="echart"></div>
  </div>
</template>
<script>
export default {
  props: {
    arry: {
      type: Object,
      default: () => [],
    },
  },
  data() {
    return {};
  },
  watch: {
    arry(val) {
      this.setEchart();
    },
  },
  mounted() {},
  methods: {
    setEchart() {
      let chart = this.$refs.echart;
      let myChart = this.$echarts.init(chart);
      myChart.clear()
      let option = {
        dataZoom: [
          {},
          {
            type: "inside",
          },
        ],
        title: {
          text: "",
        },
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: this.arry.data3,
          selected: this.arry.data4,
          type: "scroll",
          orient: "horizontal",
          left: "1%",
          top: "5%",
          textStyle: {
            color: "#fff",
          },
          pageTextStyle: {
            color: "#ffffff", //翻页数字颜色
          },
          pageIconColor: "#2C86FF", //翻页箭头颜色
          pageIconInactiveColor: "rgba(44,132,251,0.40)", //翻页（即翻页到头时箭头的颜色）
          pageIconSize: 10,
        },
        grid: {
          left: "1%",
          right: "1%",
          bottom: "2%",
          top: "15%",
          containLabel: true,
        },
        toolbox: {
          feature: {
            saveAsImage: {
              show: false,
            },
          },
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: this.arry.data1,
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          axisTick: {
            show: false,
          },
        },
        yAxis: {
          type: "value",
          axisLabel: {
            show: true,
            textStyle: {
              color: "#ffffff",
            },
          },
          splitLine: {
            show: true,
            lineStyle: {
              color: ["rgba(255, 255, 255, 0.5)"],
              type: "dashed",
            },
          },
        },
        series: this.arry.data2,
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.echart {
  flex-grow: 1;
}
.Pechart {
  height: 100%;
  display: flex;
  flex-direction: column;
}
</style>