import { request } from "./request";
import Vue from 'vue'

export function areaList() {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/areaList",
        method: 'POST'
    })
}
export function companyList(areaId) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/companyList?areaId=" +areaId,
        method: 'POST'
    })
}
export function companyDZList(companyId) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvDataAnalyseAction/companyDZList?companyId="+companyId,
        method: 'POST'
    })
}
export function generationAnalysis(areaId,companyId,dateType,fromDate,pvStationId,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvStationTwoAction/generationAnalysis?areaId="+areaId+'&companyId='+companyId+'&dateType='+dateType+'&fromDate='+fromDate+'&pvStationId='+pvStationId+'&toDate='+toDate,
        method: 'POST'
    })
}
export function companyelecstation(companyId) {
    return request({
        url: "/web/apps/gongqi.hsb.elec/elecTwoAction/companyelecstation?companyId="+companyId,
        method: 'POST'
    })
}
export function areaQYList(areaId) {
    return request({
        url: "/web/apps/gongqi.hsb.pv/pvDataAnalyseAction/areaQYList?areaId="+areaId,
        method: 'POST'
    })
}