<template>
  <div class="generationAnalysis" style="display: flex; flex-direction: column">
    <div class="right">
      <div class="search chartSearch">
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="generationAnalysisFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="generationAnalysisFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div>
          <span>园区</span>
          <el-select v-model="areaVal" placeholder="" @change="areaChange">
            <el-option
              v-for="item in areaOption"
              :key="item.areaId"
              :label="item.areaName"
              :value="item.areaId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>企业</span>
          <el-select
            v-model="companyVal"
            placeholder=""
            @change="companyChange"
          >
            <el-option
              v-for="item in companyOption"
              :key="item.companyId"
              :label="item.companyName"
              :value="item.companyId"
            >
            </el-option>
          </el-select>
        </div>
        <div>
          <span>电站</span>
          <el-select
            v-model="dzVal"
            placeholder=""
            @change="generationAnalysisFun"
          >
            <el-option
              v-for="item in dzOption"
              :key="item.pVStationId"
              :label="item.pVStationName"
              :value="item.pVStationId"
            >
            </el-option>
          </el-select>
        </div>
        <div class="tabAll">
          <div
            v-for="(item, index) in timeTab"
            :key="index"
            :class="{ isActive: index == tTIndex }"
            @click="changeTimeTab(index)"
          >
            {{ item }}
          </div>
        </div>
      </div>
      <div class="content">
        <div class="content-left border-box">
          <p class="subtitle">理论实际发电量对比</p>
          <div>
            <el-table :data="sysTmps" style="width: 100%" height="250">
              <el-table-column prop="Date" label="日期"> </el-table-column>
              <el-table-column prop="TheoryGenerateElec" label="理论发电量">
              </el-table-column>
              <el-table-column prop="ActualGenerateElec" label="实际发电量">
              </el-table-column>
              <el-table-column prop="Scale" label="PR值">
              </el-table-column>
            </el-table>
          </div>
          <div style="flex-grow: 1" class="border-box">
            <generationAnalysisChart :arry="arry1"></generationAnalysisChart>
          </div>
        </div>
        <div class="content-right border-box">
          <p class="subtitle">逆变器发电量对比</p>
          <div>
            <el-table :data="sysTmps1" style="width: 100%" height="250">
              <el-table-column prop="Date1" label="日期"> </el-table-column>
              <el-table-column prop="PVEqmId1" label="逆变器编号">
              </el-table-column>
              <el-table-column prop="PVEqmName1" label="逆变器名称">
              </el-table-column>
              <el-table-column prop="TheoryGenerateElec1" label="理论发电量">
              </el-table-column>
              <el-table-column prop="ActualGenerateElec1" label="实际发电量">
              </el-table-column>
              <el-table-column prop="Scale1" label="PR值">
              </el-table-column>
            </el-table>
          </div>
          <div style="flex-grow: 1" class="border-box">
            <generationAnalysisChart :arry="arry2"></generationAnalysisChart>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import generationAnalysisChart from "./components/generationAnalysisChart.vue";
import {
  areaList,
  companyList,
  companyDZList,
  generationAnalysis,
  companyelecstation,
  areaQYList
} from "@/network/generationAnalysis";
export default {
  components: {
    generationAnalysisChart,
  },
  data() {
    return {
      date1: "",
      date2: "",
      areaOption: [],
      areaVal: "",
      companyOption: [],
      companyVal: "",
      dzOption: [],
      dzVal: "",
      sysTmps: [],
      sysTmps1: [],
      timeTab: ["日", "月", "年"],
      tTIndex: 0,
      arry1: {},
      arry2: {},
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.areaListFun();
  },
  methods: {
    /**
     * 获取园区下拉
     * **/
    areaListFun() {
      areaList()
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.areaOption = content.filter(item=> item.isPV);
            this.areaVal = this.areaOption[0].areaId;
            this.companyListFun();
            this.generationAnalysisFun();
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取企业下拉
     * **/
    companyListFun() {
      areaQYList(this.areaVal)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.companyOption = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 获取电站名称
     * **/
    companyDZListFun() {
      companyDZList(this.companyVal)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.dzOption = content;
          }
        })
        .catch((err) => {});
    },
    changeTimeTab(index) {
      this.tTIndex = index;
      this.generationAnalysisFun();
    },
    generationAnalysisFun() {
      generationAnalysis(
        this.areaVal,
        this.companyVal,
        this.timeTab[this.tTIndex],
        this.date1,
        this.dzVal,
        this.date2
      )
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.sysTmps = content.sysTmps;
            this.sysTmps1 = content.sysTmps1;
            this.arry1 = {
              data1: content.data1_1,
              data2: content.data1_2,
              data3: content.data1_3,
              data4: "",
            };
            this.arry2 = {
              data1: content.data2_1,
              data2: content.data2_2,
              data3: content.data2_3,
              data4: content.data2_4,
            };
          }else{
          }
        })
        .catch((err) => {});
    },
    /**切换园区**/
    areaChange() {
      this.companyListFun();
      this.generationAnalysisFun();
    },
    /**切换企业**/
    companyChange() {
      this.dzVal = ''
      this.companyDZListFun();
      this.generationAnalysisFun();
    },
  },
};
</script>
